import DxfReader from './dxf-parser/DxfScanner'
import Utils from './utils/utils'

const file = <HTMLInputElement>document.getElementById('file')
const fileButton = <HTMLButtonElement>document.getElementById('fileButton')
const LayerDistanceElement = <HTMLOListElement>document.getElementById('distanceOnLayer')
let reader = new DxfReader()


const ParseLinesDxfFile = () => {
    let fileToLoad = file.files[0]
    let fileReader = new FileReader()
    fileReader.onload = function (fileLoadedEvent) {
        // console.log(fileLoadedEvent.target.result.toString())
        reader.GetDxfFile(fileLoadedEvent.target.result.toString())
        const LinesByLayer = reader.getLines()
        // const distanceBylayer = {}
        console.log(LinesByLayer)

        for (const layer in LinesByLayer) {
            let distance = 0
            for (const layerLinesKey of LinesByLayer[layer]) {
                distance += Utils.GetDistanceBetween2Point(
                    layerLinesKey.point1,
                    layerLinesKey.point2
                )
            }
            // distanceBylayer[layer] = distance
            console.log(layer)
            let liLayer = <HTMLElement>document.createElement('li')
            liLayer.id = `${layer}`

            const nameList = <HTMLSpanElement>document.createElement('span')
            nameList.innerText = `${layer}  :`

            const valueList = <HTMLSpanElement>document.createElement('span')
            valueList.innerText = `${distance}`

            liLayer.appendChild(nameList)
            liLayer.appendChild(valueList)

            LayerDistanceElement.appendChild(liLayer)
        }


    }
    fileReader.readAsText(fileToLoad, 'UTF-8')
}

fileButton.addEventListener('click', ParseLinesDxfFile)
